/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box; }

.page {
  font-family: Arial;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden; }
  .page__nav {
    flex-shrink: 0;
    background-color: #17141f;
    width: 250px;
    height: 100%;
    padding-top: 5%; }
    .page__nav .nav__item {
      padding: 10px;
      font-size: 1.1em;
      color: #cbc8d0;
      cursor: pointer; }
      .page__nav .nav__item:hover {
        background-color: #5c507c; }
  .page__main {
    flex-shrink: 1;
    width: calc(100% - 250px - 350px);
    height: 100%;
    margin: 40px;
    padding-top: 30px; }
    .page__main .viewer__container {
      width: 100%;
      height: 60%;
      display: flex;
      align-items: center;
      justify-content: center; }
      .page__main .viewer__container .main__viewer {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-color: black; }
        .page__main .viewer__container .main__viewer .viewer__kappa {
          transition: all .25s;
          cursor: pointer;
          width: 60%;
          max-width: 500px; }
        .page__main .viewer__container .main__viewer .viewer__kappa:active {
          transform: scale(0.9); }
    .page__main .viewer__stats {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-top: 10px; }
      .page__main .viewer__stats .stats__number {
        font-size: 1.2em;
        color: indigo; }
        .page__main .viewer__stats .stats__number--kpm {
          margin-right: auto; }
    .page__main .shop__container {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-wrap: wrap; }
      .page__main .shop__container .shopitem {
        position: relative;
        text-align: center;
        color: indigo;
        width: 350px;
        height: 225px;
        border: solid 2px indigo;
        margin: 10px;
        font-size: 1.3em; }
        .page__main .shop__container .shopitem .shopitem__buy {
          position: absolute;
          top: 0;
          right: 5px;
          font-size: 2em;
          cursor: pointer; }
        .page__main .shop__container .shopitem .shopitem__container {
          font-size: 1.5em; }
        .page__main .shop__container .shopitem__infocontainer {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: flex-end; }
          .page__main .shop__container .shopitem__infocontainer .shopitem__info {
            margin: 10px; }
            .page__main .shop__container .shopitem__infocontainer .shopitem__info--cost {
              margin-right: auto; }
        .page__main .shop__container .shopitem__title {
          margin-top: 20px;
          padding-bottom: 20px;
          border-bottom: solid 2px indigo; }
    @media screen and (max-width: 930px) {
      .page__main {
        width: calc(100% - 250px); } }
  .page__chat {
    flex-shrink: 0;
    background-color: #efeef1;
    width: 350px;
    height: 100%;
    overflow: auto;
    position: relative; }
    .page__chat .chat__header {
      background-color: inherit;
      position: absolute;
      right: 0px;
      top: 0px;
      height: 50px;
      font-size: 1.2em;
      width: 335px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-bottom: solid 1px gray; }
    .page__chat .chat__placeholder {
      height: 50px;
      width: 100%; }
    .page__chat .chat__messages {
      margin: 15px; }
      .page__chat .chat__messages .message {
        font-size: 1.1em;
        margin: 10px;
        display: flex;
        align-items: center;
        justify-content: flex-start; }
        .page__chat .chat__messages .message .message__kappa {
          height: 32px;
          width: 32px; }
        .page__chat .chat__messages .message .message__badge {
          height: 16px;
          width: 16px; }
    @media screen and (max-width: 930px) {
      .page__chat {
        display: none; } }

.credits {
  font-size: 2em;
  margin: 20px; }
  .credits p {
    padding: 10px; }
  .credits a {
    text-decoration: none; }

@keyframes floatAnimation {
  from {
    opacity: 1;
    transform: translateY(0px); }
  to {
    opacity: 0;
    transform: translateY(-30px); } }

.floating {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  animation: floatAnimation 1s linear;
  font-size: 3em;
  color: white;
  position: absolute; }
